// import './App.scss';
import { Card, Button, Fade, ButtonGroup, Form, Alert, InputGroup, FormControl, Accordion } from 'react-bootstrap'
// import 'bootstrap/dist/css/bootstrap.min.css';
import WinePlate from './WinePlate';
import React, { useEffect, useState } from 'react';
import axios from 'axios'
import * as Icon from 'react-bootstrap-icons';
// import TagSearch from './components/TagSearch';
import { createFilter } from 'react-select';
import CreatableSelect from 'react-select/creatable'
// import { FixedSizeList as List } from "react-window";
import CustomOption from './CustomComponent';
import CustomMenuList from './CustomMenuList';

const MAIN_COLOR = '#e7154e'
const SECONDARY_COLOR = '#fce3ee'

const courses = [
  "appetizer",
  "main",
  "dessert"
]

const types = {
  'red': '#731624',
  'white': '#fadb7d',
  'sparkling': '#305432',
  'rose': '#e0c8df',
  'desert': '#adbdd9'
}

const pairs = ['grilled food', 'pork', 'mutton, lamb', 'beef', 'savoury snacks',
  'casseroles', 'chicken, turkey', 'lean fish',
  'salads, vegetarian food', 'pasta and pizza', 'party wine',
  'oriental food', 'buffet', 'strong cheeses', 'game', 'fatty fish',
  'mild cheeses', 'spicy and meaty sausages', 'sushi',
  'mild sausages', 'tapas and antipasti', 'berries and fruits',
  'hot food', 'seafood', 'blini', 'sweet desserts',
  'clams, mussels, scallops and oysters', 'chocolate desserts',
  'mushrooms', 'soups', 'liver', 'game birds']

function Body() {
  const [meal, setMeal] = useState('');
  const [pair, setPair] = useState('');
  const [recipe, setRecipe] = useState({});
  const [wining, setWining] = useState(false);
  const [advSearch, setAdvSearch] = useState(false);
  const [allWines, setAllWines] = useState([]);
  const [displayedWines, setDisplayedWines] = useState([]);
  const [searchErr, setSearchErr] = useState('');
  const [width, setWidth] = useState(window.innerWidth);
  const [priceRange, setPriceRange] = useState('$$');
  const [searchVisible, setSearchVisible] = useState(true);

  const [selectedTags, setSelectedTags] = useState([]);
  const [phrases, setPhrases] = useState([]);
  const [recipeType, setRecipeType] = useState('');
  const [isMulti, setIsMulti] = useState(true);

  const wineMeRef = React.createRef();

  useEffect(() => {
    readFile('phrases')
  }, [])

  const priceIndex = (level) => {
    return {
      '$': 0,
      '$$': 1,
      '$$$': 2
    }[level]
  }

  const handlePriceLevelChange = (level) => {
    setPriceRange(level)
    setDisplayedWines(allWines[priceIndex(level)])
    // setWining(true)

    // const IMAGES = allWines[priceIndex(level)].map(w => imageLink(w.link))
    // Promise.all(IMAGES.map(image => image ? loadImage(image) : null))
    //   .then(() => {
    //     setDisplayedWines(allWines[priceIndex(level)])
    //     setWining(false)
    //   })
    //   .catch(err => console.log("Failed to load images", err))
  }

  const loadWines = (data) => {
    const wines = data.wines

    data.recipe && setRecipe(data.recipe)
    if (wines.length == 3) {
      setAllWines(
        wines
      )
      setDisplayedWines(wines[priceIndex(priceRange)])
    } else {
      setDisplayedWines(wines)
    }

    const IMAGES = wines.flat().map(w => imageLink(w.link))
    Promise.all(IMAGES.map(image => image ? loadImage(image) : null))
      .then(() => {
        setWining(false)
        data.recipe && setSearchVisible(false)
      })
      .catch(err => console.log("Failed to load images", err))
  }

  const imageLink = (link) => {
    let productNumber = link.split('/')[5]
    let productName = link.split('/')[6]
    return `https://images.alko.fi/images/cs_srgb,f_auto,t_products/cdn/${productNumber}/${productName}.jpg`
  }

  function isLink(text) {
    var expression = /[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)?/gi;
    var regex = new RegExp(expression)

    if (text.match(regex)) {
      return true
    } else
      return false
  }

  const loadImage = image => {
    return new Promise((resolve, reject) => {
      const loadImg = new Image()
      loadImg.src = image
      // wait 2 seconds to simulate loading time
      loadImg.onload = () =>
        setTimeout(() => {
          resolve(image)
        }, 2000)

      loadImg.onerror = err => reject(err)
    })
  }

  function onlyUnique(value, index, self) {
    return self.indexOf(value) === index;
  }

  function readFile(file) {
    fetch(`Roznavene-project-demo/data/${file}.txt`)
      .then(response => response.text())
      .then(text => {
        var phraseList = text.split(',\r\n')
        phraseList = phraseList.map(e => {
          return e.replace(/[^a-zA-Z ]+/g, '').trim()
        }).filter(e => e).filter(onlyUnique)

        setPhrases(phraseList)
      })
  }

  const optionObject = (option) => {
    return { value: option, label: option.replace(/\b\w/g, l => l.toUpperCase()) }
  }

  function options() {
    return phrases.map(o => optionObject(o))
  }

  function WineDisplay() {
    return (
      <div
        className="no-scrollbar"
        id="wine-display"
        style={{ width: '100%' }}>
        {
          displayedWines ? displayedWines.map(wine =>
            WinePlate(wine)
          ) : <span>No recommendations in this price range.</span>
        }
      </div>
    )
  }

  // const winelistHeight = () => {
  //   if (wining)
  //     return '0px'
  //   else if (pair) {
  //     return isMobile ? `calc(100vh - ${80 + 80 + 2 * 16 + 67 + 50}px)` : '500px'
  //   }
  //   else return '0px'
  //   // (pair && !wining && !searchErr) ? isMobile ? `calc(100vh - ${80 + 80 + 2 * 16 + 67 + 50}px)` : '500px' : '0px'
  // }

  const wineMeDisabled = () => {
    return !selectedTags.length
  }

  const handleWineMe = (payload) => {
    let link = payload[0].value

    setPair(link)
    setWining(true)

    const options = {
      method: 'POST',
      url: 'https://pocketsomm.dev/api/v1/link2wine',
      headers: { 'Content-Type': 'application/json' },
      data: { link: link }
    };

    axios.request(options).then(function (response) {
      setSearchErr(false)
      console.log(response.data)
      loadWines(response.data)
    }).catch(function (error) {
      setSearchErr(error.message)
      console.log(error.response.data.detail)
      loadWines(error.response.data.detail)
    });

    // const wines = rows.filter(e => e.pair && e.pair.toUpperCase() === meal.toUpperCase())
    // setDisplayedWines(
    //   wines
    // )

    // if (wines.length == 0)
    //   setTimeout(
    //     function () {
    //       if (wines.length == 0)
    //         setSearchErr(`"${meal}" is not a supported dish.\n Try searching "${pairs[Math.floor(Math.random() * pairs.length)]}" instead`)
    //       else
    //         setSearchErr(false)
    //       setWining(false)
    //     },
    //     1000
    //   )
    // else {
    //   setSearchErr(false)
    //   const IMAGES = wines.map(w => imageLink(w.link))

    //   Promise.all(IMAGES.map(image => image ? loadImage(image) : null))
    //     .then(() => {
    //       setWining(false)
    //     })
    //     .catch(err => console.log("Failed to load images", err))
    // }
  }

  const handleInputChange = (input) => {
    // console.log('input:', input)
    // // if (recipeType !== 'link' && !input){
    // //   setRecipeType('')
    // //   console.log(`${input.length} none`)
    // // }
    // if (isLink(input)) {
    //   setRecipeType('link')
    // } else if (!selectedTags.length) {
    //   setRecipeType('')
    // }
  }

  const toLinkMode = (input) => {
    setSelectedTags(
      (!Array.isArray(input) && input) || input[input.length - 1]
    )
    setIsMulti(false)
    handleWineMe(input)
  }

  const toTagMode = (input) => {
    setSelectedTags(input)
    setIsMulti(true)
  }

  const handleTagChange = (tags, action) => {
    // console.log(tags, action)
    if (!tags || !tags.length) {
      setSelectedTags([])
      setIsMulti(true)
      return
    }

    let isObject = !Array.isArray(tags)

    if ((isObject && isLink(tags.value)) || (isLink(tags[tags.length - 1].value))) {
      toLinkMode(tags)
    } else {
      toTagMode(tags)
    }
  }

  const resetApp = () => {
    setPair('');
    setSearchVisible(true);
    setPriceRange('$$');
    setMeal('')
    // document.getElementById(
    //   'inlineFormInputGroup').value = ''
  }

  return (
    <div style={{ height: '100%', width: '100%', padding: '10px', display: 'flex', flexDirection: 'column' }}>

      <div id="body-header" style={{ height: '140px' }}>
        <div id="body-logo" style={{ height: '60px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
          <img onClick={resetApp} className="clickable nodrag" src="/Roznavene-project-demo/logo.svg" width="200px"></img>
        </div>
        <div id="body-query" style={{ height: '80px', width: '80%', margin: 'auto', textAlign: 'start' }}>
          {/* <Fade in={!recipe['title'] || wining}> */}
          <Form.Label
            onClick={e => { console.log(displayedWines) }}
            style={{
              textAlign: 'start',
              fontSize: '14px',
              marginBottom: '0px'
            }}>
            WHAT ARE YOU EATING?
          </Form.Label>
          {/* </Fade> */}
          {/* <Form.Label style={{ width: '79%', textAlign: 'start', fontSize: recipe['title'] ? '16px' : '14px' }}>
                {recipe['title'] ? <b>{recipe['title'].toUpperCase()}</b> : 'WHAT ARE YOU EATING?'}
              </Form.Label> */}

          <Fade in={!searchVisible}>
            <div style={{ display: !searchVisible ? '' : 'none' }}>
              <Form.Label style={{
                textAlign: 'start',
                fontSize: '16px'
              }}>
                <b>{recipe['title'] ? recipe['title'].toUpperCase() : ''}</b>
              </Form.Label>
              <Icon.X
                className="clickable nodrag"
                style={{ marginLeft: '5px' }}
                size={20}
                color='gray'
                onClick={e => { setSearchVisible(true) }} />
            </div>
          </Fade>

          <Fade in={searchVisible}>
            <div style={{ display: searchVisible ? '' : 'none', padding: '5px' }}>
              <CreatableSelect
                value={selectedTags}
                onChange={handleTagChange}
                onInputChange={handleInputChange}
                captureMenuScroll={false}
                classNamePrefix={`clickable custom-select`}
                filterOption={createFilter({ ignoreAccents: false })}
                openMenuOnClick={false}
                isMulti={isMulti}
                placeholder="select tags or paste recipe link"
                components={{
                  Option: CustomOption,
                  MenuList: CustomMenuList
                }}
                options={options()}
                isClearable
              />
            </div>
            {/* <InputGroup style={{
                    display: searchVisible ? '' : 'none',
                    margin: 'auto'
                  }} >
                    <InputGroup.Text>🍽️</InputGroup.Text>
                    <FormControl onChange={e => setMeal(e.target.value)} id="inlineFormInputGroup" placeholder="enter recipe link" />
                  </InputGroup> */}
          </Fade>
          {
            (isLink(meal) && meal) ? <div style={{ width: '80%', margin: 'auto' }}>
              <span style={{ color: 'darkred', float: 'left', fontSize: '12px', margin: '2px' }}>
                * corked link
              </span>
            </div> : null
          }
          <div style={{ height: '24px', display: searchErr && !wining ? '' : 'none' }}>
            <span style={{ fontSize: '13px', color: 'darkred' }}>{searchErr}</span>
          </div>
        </div>
      </div>

      <Fade id="body-winelist-$-levels" in={!wining && !!pair && !searchErr}>
        <div style={{
          display: !wining && !!pair && !searchErr ? 'flex' : 'none',
          height: '25px',
          alignItems: 'center',
          justifyContent: 'center'
        }}>
          <ButtonGroup aria-label="Basic example">
            {
              ["$", "$$", "$$$"].map(level =>
                <Button
                  key={level}
                  onClick={e => {
                    handlePriceLevelChange(level)
                  }}
                  size="sm"
                  variant="outline-danger"
                  active={priceRange == level}
                  style={{
                    padding: '0px',
                    width: '45px',
                    height: '20px',
                    fontSize: '14px',
                    lineHeight: '18px'
                  }}
                >{level}
                </Button>
              )
            }
          </ButtonGroup>
        </div>
      </Fade>
      <div id="body-winelist"
        className="animated winelist"
        style={{
          width: '100%',
          // height: winelistHeight(),
          selfAlign: 'start',
          overflowY: 'auto',
          overflowX: 'hidden'
        }}>
        {
          ['$', '$$', '$$$'].map(e =>
            <Fade key={`${e}_display`} in={!wining && priceRange === e}>
              <div style={{ display: !wining && priceRange === e ? '' : 'none' }}>
                <WineDisplay />
              </div>
            </Fade>
          )
        }
      </div>


      <div style={{ margin: 'auto' }}></div>
      <div id="body-footer" style={{
        width: '100%',
        height: '55px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'end'
      }}>
        <Button
          type="submit"
          disabled={wineMeDisabled()}
          onClick={handleWineMe}
          variant="danger"
          ref={wineMeRef}
          style={{ width: '100%', height: '45px', zIndex: 0 }}>
          <span>
            {
              wining ? <div>
                <div className="rotate-center" style={{ display: 'inline-flex', marginRight: '8px' }}>🍷</div><span>PAIRING...</span>
              </div>
                : 'WINE ME'
            }
          </span>
          {/* {
                wining ? <div>
                  <span>🍷</span>
                  <span>PAIRING...</span>
                </div> : <span>WINE ME</span>
              } */}
          {/* <Loadable loading={wining} component={<span>WINE ME </span>} /> */}
        </Button>
      </div>

    </div>
  );
}

export default Body;
